<template>
    <div class="viewport">
        <div class="row">
            <div class="col-lg-12">
                <blockquote>
                    <p class="text-primary">bug反馈 
                    <!--<button class="btn btn-link pull-right" v-show="!fbCBInfoPanelShow">{{type_str}}</button>-->
                    </p>
                </blockquote>
            </div>
        </div>
        <div class="center-form" v-show="!fbCBInfoPanelShow">
            <div class="row">
                <form class="form-horizontal" id="appform" enctype="multipart/form-data">
                    <input type="hidden" name="type" value="5">

                    <div class="form-group" :class="{'has-error':description_err}">
                        <label class="col-lg-3 control-label"><span class="text-warning">*</span> 问题描述：</label>
                        <div class="col-lg-8">
                            <input type="text" name="description" v-model="description" placeholder="请不要超过20个文字" class="form-control">
                        </div>
                    </div>

                    <div class="form-title m-t-lg">
                        <div class="form-title-l-txt">描述</div>
                        <div class="form-title-line"></div>
                        <div class="form-title-r-txt"></div>
                    </div>

                    <div class="form-group" :class="{'has-error':bug_description_err}">
                        <label class="col-lg-3 control-label"><span class="text-warning">*</span> bug描述：</label>
                        <div class="col-lg-8">
                            <textarea name="" v-model="bug_description" name="detail[bug_description]" class="form-control" placeholder="多行输入" rows="5"></textarea>
                        </div>
                    </div>

                    <div class="form-group">
                        <label class="col-lg-3 control-label">日志上传：</label>
                        <div class="col-lg-8">
                            <FileInput ipname="log_file" v-model="log_file"></FileInput>
                        </div>
                    </div>

                    <div class="form-group" :class="{'has-error':recurrence_probability_err}">
                        <label class="col-sm-3 control-label"><span class="text-warning">*</span> 复现概率：</label>
                        <div class="col-sm-8">
                            <label class="radio-inline">
                                <input type="radio" value="1" name="detail[recurrence_probability]" v-model="recurrence_probability">偶现</label>
                            <label class="radio-inline">
                                <input type="radio" value="2" name="detail[recurrence_probability]" v-model="recurrence_probability">必现</label>
                        </div>
                    </div>

                    <div class="form-title m-t-lg" v-if="type_str=='market'">
                        <div class="form-title-l-txt">环境</div>
                        <div class="form-title-line"></div>
                        <div class="form-title-r-txt"></div>
                    </div>

                    <div class="form-group" :class="{'has-error':sdk_version_err}" v-if="type_str=='market'">
                        <label class="col-lg-3 control-label"><span class="text-warning">*</span> SDK版本：</label>
                        <div class="col-lg-8">
                            <input type="text" name="detail[sdk_version]" v-model="sdk_version" class="form-control">
                        </div>
                    </div>

                    <div class="form-group" :class="{'has-error':equipment_model_err}" v-if="type_str=='market'">
                        <label class="col-lg-3 control-label"><span class="text-warning">*</span> 设备型号：</label>
                        <div class="col-lg-8">
                            <input type="text" name="detail[equipment_model]" v-model="equipment_model" class="form-control">
                        </div>
                    </div>

                    <div class="form-group" :class="{'has-error':android_version_err}" v-if="type_str=='market'">
                        <label class="col-lg-3 control-label"><span class="text-warning">*</span> 安卓版本</label>
                        <div class="col-lg-8">
                            <input type="text" name="detail[android_version]" v-model="android_version" class="form-control">
                        </div>
                    </div>

                    <div class="form-group" :class="{'has-error':architecture_err}" v-if="type_str=='market'">
                        <label class="col-lg-3 control-label"><span class="text-warning">*</span> 运行架构</label>
                        <div class="col-lg-8">
                            <input type="text" name="detail[architecture]" v-model="architecture" class="form-control">
                        </div>
                    </div>
                    
                    <div class="form-group">
                        <div class="col-lg-offset-3 col-lg-3 text-left">
                            <button class="btn btn-primary btn-block" @click.prevent="checkAddFeedback">提 交</button>
                        </div>
                    </div>
                </form>
            </div>
        </div>
        <Modal v-if="confirmFBPopShow" @close="confirmFBPopShow=false">
            <div slot="header">
                <h4 class="modal-title text-center">确认提交该条反馈吗</h4>
            </div>
            <div slot="body" class="text-center">
                <p>更详细的描述,有助于问题更快地定位和排查,请尽量填写完整信息。</p>
            </div>
            <div slot="footer">
                <button type="button" class="btn btn-primary" @click="confirmAddFB()" :disabled="adding">
                <i class="fa fa-refresh fa-spin fa-fw" v-show="adding"></i>
                确 定
                </button>
            </div>
        </Modal>

        <FbCBInfoPanel v-show="fbCBInfoPanelShow" :fbNumber="fbNumber" :fbId="fbId" @goOnFb="goOnFb()"></FbCBInfoPanel>

    </div>
</template>
<script>
import FileInput from '../components/FileInput.vue'
import DateTimePicker from '../components/DateTimePicker.vue'
import Modal from '../components/Modal.vue'
import FbCBInfoPanel from '../components/FbCBInfoPanel.vue'

import {
        tip,
        ajax
    } from '../tools/tools.js'

export default {
    components:{FileInput,DateTimePicker,Modal,FbCBInfoPanel},
    data(){
        return {
            fd:null,

            type_str:'market',

            description:'',
            description_err:false,
            bug_description:'',
            bug_description_err:false,
            log_file:null,
            recurrence_probability:'',
            recurrence_probability_err:false,

            sdk_version:'',
            sdk_version_err:false,
            equipment_model:'',
            equipment_model_err:false,
            android_version:'',
            android_version_err:false,
            architecture:'',
            architecture_err:false,

            confirmFBPopShow:false,
            fbId:'',
            fbNumber:'',
            fbCBInfoPanelShow:false,

            adding:false
        }
    },
    watch:{

    },
    methods:{
        checkAddFeedback(){
            var self = this;

            var isPass = true;

            if ($.trim(self.description).length == 0||$.trim(self.description).length > 20) {
                self.description_err = true;
                isPass = false;
            } else {
                self.description_err = false;
            }
            if ($.trim(self.bug_description).length == 0) {
                self.bug_description_err = true;
                isPass = false;
            } else {
                self.bug_description_err = false;
            }
            if ($.trim(self.recurrence_probability).length == 0) {
                self.recurrence_probability_err = true;
                isPass = false;
            } else {
                self.recurrence_probability_err = false;
            }
            
            if(self.type_str=='market'){
                if ($.trim(self.sdk_version).length == 0) {
                    self.sdk_version_err = true;
                    isPass = false;
                } else {
                    self.sdk_version_err = false;
                }
                if ($.trim(self.equipment_model).length == 0) {
                    self.equipment_model_err = true;
                    isPass = false;
                } else {
                    self.equipment_model_err = false;
                }
                if ($.trim(self.android_version).length == 0) {
                    self.android_version_err = true;
                    isPass = false;
                } else {
                    self.android_version_err = false;
                }
                if ($.trim(self.architecture).length == 0) {
                    self.architecture_err = true;
                    isPass = false;
                } else {
                    self.architecture_err = false;
                }
            }

            if(isPass){
                self.fbNumber = '';
                self.confirmFBPopShow = true;
            } else {
                tip.err('请填写必填项');
            }            

        },
        confirmAddFB(){
            var self = this;
            self.fd = new FormData(document.getElementById('appform'));
            self.adding = true;
            ajax.addFeedback({
                param: self.fd,
                cb:function(data){
                    self.fbNumber = data.data.number;
                    self.confirmFBPopShow = false;
                    self.fbCBInfoPanelShow = true;
                    self.adding = false;
                },
                failCb:function(data){
                    self.adding = false;
                }
            });

            // self.fbNumber = '1';
            // self.confirmFBPopShow = false;
            // self.fbCBInfoPanelShow = true;
        },
        goOnFb(){
            var self = this;
            self.description = '';
            self.description_err = false;
            self.bug_description = '';
            self.bug_description_err = false;
            self.log_file = null;
            self.recurrence_probability = '';
            self.recurrence_probability_err = false;

            self.sdk_version = '';
            self.sdk_version_err = false;
            self.equipment_model = '';
            self.equipment_model_err = false;
            self.android_version = '';
            self.android_version_err = false;
            self.architecture = '';
            self.architecture_err = false;

            self.fbNumber = '';
            self.fbCBInfoPanelShow = false;
        }
    },
    mounted(){
        var self = this;
        self.type_str = self.$root.USE_SCENE == 'market'?'market':'sdk';
    }
}
</script>
<style scoped>

</style>



